import axios from "axios";


export const initializeLiff = async ({ setIintLine, setLoading, liffId }: { setIintLine: React.Dispatch<React.SetStateAction<boolean>>, setLoading: React.Dispatch<React.SetStateAction<boolean>>, liffId: string, }) => {
    try {
        await new Promise((resolve) => setTimeout(resolve, 3000));
        const liff = (await import('@line/liff')).default;
        await liff.init({ liffId });
        setIintLine(true);
        setLoading(false);
    } catch (error) {
        throw error;
    }
};


export const registerUserByTaxId = async (taxId: string, user_id: string) => {
    try {
        let config = {
            method: 'post',
            maxBodyLength: Infinity,
            url: `http://192.168.64.92:4004/api/line/registerRichMenuToUser?tax_id=${taxId}&user_id=${taxId}`,
            headers: {}
        };
        return await axios.request(config);
    } catch (error) {
        throw error;
    }
}

