'use client'

import React, { useEffect, useState } from 'react'
import { UserCheck } from "@phosphor-icons/react";
import Grid from '@mui/material/Grid2';
import useTheme from '@mui/material/styles/useTheme';
import { Box, TextField, Typography } from '@mui/material';

import { BaseButton } from '@/components/Button';
import Loading from './Loading';
import { initializeLiff } from '../services/regisger_service';
import { useForm } from "react-hook-form";
import { SignInFormData, SignInSchema } from '@/schemas/SignInSchema';
import { zodResolver } from '@hookform/resolvers/zod';
import { log } from 'console';



const RegisterForm = () => {
    const liffId = process.env.NEXT_PUBLIC_LIFF_ID || "";
    const [isIintLine, setIintLine] = useState(false);
    const [isLoading, setLoading] = React.useState(false);
    const theme = useTheme();
    const u = theme.palette.background.default;
    let userId = "";
    useEffect(() => {
        setLoading(true);
        initializeLiff({ setIintLine, setLoading, liffId });

    }, []);



    const regisger = async () => {
        setLoading(true);
        await new Promise((resolve) => setTimeout(resolve, 3000));
        setLoading(false);
    }

    const {
        register,
        handleSubmit,
        formState: { errors },
    } = useForm<SignInFormData>({
        resolver: zodResolver(SignInSchema),
    });

    const onSubmit = (data: SignInFormData) => {
        data.user_id = userId;
        log("data");
        regisger();
    };


    return (

        <Box>
            <Loading open={isLoading} />
            {isIintLine ? (
                <Grid container spacing={1}
                    alignItems="center"
                    justifyContent="center"
                    display="flex"
                    height="100vh"
                    sx={{ backgroundColor: u }}>

                    <Grid size={{ xs: 12, md: 6 }} sx={{ px: { xs: 5, md: 0 } }}>
                        <Box sx={{ py: 5, bgcolor: "white", px: 5, borderRadius: 4 }} component="form" onSubmit={handleSubmit(onSubmit)}>
                            <Box sx={{ py: 1.5 }} alignItems="center" justifyContent="center" textAlign="center">
                                <Typography sx={{ fontSize: '1rem' }}>ลงทะเบียน</Typography>
                            </Box>

                            <TextField
                                {...register("tax_id", { required: "กรุณากรอกเลขประจำตัวผู้เสียภาษี" })}
                                id="standard-basic"
                                placeholder='เลขประจำตัวผู้เสียภาษี'
                                helperText={errors.tax_id?.message}
                                error
                                fullWidth
                                slotProps={
                                    {
                                        input: {
                                            endAdornment: <UserCheck size={30} />
                                        }
                                    }
                                }
                            />
                            {errors.tax_id && errors.tax_id.type === "required" && (
                                <p className="text-red-500 text-sm">{errors.tax_id && errors.tax_id.type === "required"}</p>
                            )}
                            <Box sx={{ py: 1.5 }}  >
                                <BaseButton text='ลงทะเบียน' color='primary' variant='contained' fullWidth type='submit' />
                            </Box>

                        </Box>
                    </Grid>
                </Grid >
            ) : null
            }

        </Box>
    );


}

export default RegisterForm
